$full-size: 100%;
$red: #D2132A;
$default-font-size: 16px;
$flex: flex;

$archivo-regular: "Archivo-Regular";
$archivo-bold: "Archivo-Bold";
$archivo-black: "ArchivoBlack-Regular";


@font-face {
    font-family: "Archivo-Regular";
    src: local("Archivo Regular"), local("Archivo-Regular"),
        url(../fonts/Archivo-Regular.woff2) format('woff2'),
        url(../fonts/Archivo-Regular.woff) format('woff'),
        url(../fonts/Archivo-Regular.ttf) format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "Archivo-Bold";
    src: local("Archivo Bold"), local("Archivo-Bold"),
        url(../fonts/Archivo-Bold.woff2) format('woff2'),
        url(../fonts/Archivo-Bold.woff) format('woff'),
        url(../fonts/Archivo-Bold.ttf) format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "ArchivoBlack-Regular";
    src: local("Archivo Black Regular"), local("ArchivoBlack-Regular"),
        url(../fonts/ArchivoBlack-Regular.woff2) format('woff2'),
        url(../fonts/ArchivoBlack-Regular.woff) format('woff'),
        url(../fonts/ArchivoBlack-Regular.ttf) format('truetype');
    font-weight: normal;
    font-style: normal;
}


* {
    margin: 0;
    padding: 0;

    &:before, :after{
        box-sizing: inherit;
    }
}

html{
    box-sizing: border-box;
}

body{
    width: $full-size;
    min-width: 320px;
    color: $red;
    font-family: $archivo-regular;
    font-size: $default-font-size;
}

a{
    text-decoration: none;
}



main{
    position: relative;
    // overflow: hidden;
}

// img{
//     width: 100%;
//     height: auto;
//     display: block;
// }

.container{
    width: 100%;
    max-width: 1920px;
    height: inherit;

    &__grid{
        display: grid;
        

        &-2__col{
            grid: auto/ 1fr;
        }
    }
}

.header{
    display: $flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    padding: 0 2em;
    min-height: $full-size/10;

    &__contact{

        margin: 0 11px;
    }

    &__contact-link{
        font-weight: bold;
        font-size: 12px;
        line-height: 13px;
        /* identical to box height */

        text-transform: uppercase;

        color: #FFEFF6;
    }

    .contact-link--dark{
        color: #161415;
    }

}

.menu{
    margin: 0 11px;
}

.menu--circle{
    width: 54px;
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 25px;
    background: white;
    margin: 0;
}

.nav{
    display: flex;
    align-items:center;
    justify-content: center;
    height: 30px;
}


.hamburger{
    width: 18px;
    height: 18px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    &__item{
        $item-size: 7.5px;
        width: $item-size;
        height: $item-size;
        background: url("../img/svg/lettering.svg");
    }
}

.section{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
     justify-content: center;
    padding: 2rem 0;

    &__inner{
         width: inherit;        
     }
}

.first-section{    
    height: 100vh;
    left: 0px;
    top: 0px;
    background: radial-gradient(48.18% 24.84% at 50% 50%, #2A0227 0%, #170119 100%);
    padding: 0;
}

#main-text{
    perspective: 60px;
}


.screen{
    width: $full-size;
    height: $full-size*0.9;
    display: $flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    position: relative;

    .text, .counter{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: $full-size;
    }

    .text{
        position: relative;
        width: auto;
    }


    div:nth-child(1){
        flex: 3 1 100%;
    }
    .counter{
        flex: 1 1 5rem;
    }

    .text__simple{
        font-family: $archivo-regular;
        font-weight: bold;
        font-size: $default-font-size - 2;
        line-height: $default-font-size - 1;
        /* identical to box height */

        text-transform: uppercase;

        color: #F9A608;
    }

    .text__title{
        font-family: $archivo-black;
        font-style: normal;
        font-weight: normal;
        font-size: $default-font-size*3.5;
        line-height: $default-font-size*3.5+ 10;
        /* identical to box height */

        text-align: center;
        text-transform: uppercase;

        color: #FFEFF6;
    }

    .image__container{
        position: absolute;
        bottom: 30%;
        padding: $default-font-size;
        .text__image{
            max-width: 320px;
            width: 100%;
        }
    }

    .counter{

        margin: 3rem 1rem;

        &__description{
            font-family: $archivo-regular;
            font-size: $default-font-size;
            line-height: $default-font-size + 1;
            color: #643C5A;
            margin: 11px;

        }

        &__numbers{
            font-family: $archivo-regular;
            font-weight: bold;
            font-size: $default-font-size*2 + 2;
            line-height: $default-font-size*3 +1;
            /* identical to box height */
            letter-spacing: 2px;
            text-transform: uppercase;
            color: #F6F6F6;
        }

        &__text{
            font-family: $archivo-regular;
            font-weight: 500;
            font-size: $default-font-size;
            line-height: $default-font-size + 1;
            text-transform: uppercase;
            color: #F6F6F6;
        }
    }

    
    
}



.section__clients{   
    
    background: #ffeff6;

    &-grid{
        grid: auto 1fr / 1fr;
    }

    &-cards{
    display: flex;
    justify-content: flex-start;
    padding: 2em;
    }

    &-description{
        display: $flex;
        flex-direction: column;
        padding: 2em;
    }

    .clients-description__title{
        font-family: $archivo-black;
        font-size: $default-font-size*3 - 2;
        line-height: $default-font-size*3 - 2;
        /* or 100% */
        color: #383030;
        max-width: 50%;
        margin: 11px 0;        
    }

    .clients-description__text{
        font-family: $archivo-regular;
        font-size: $default-font-size;
        line-height: $default-font-size + 10;
        /* or 162% */

        color: #643C5A;

        mix-blend-mode: normal;
        opacity: 0.5;

        margin: 11px 0 22px 0;

        max-width: 80%;
    }

    .clients-description__link{
        font-family: $archivo-black;
        font-size: $default-font-size - 4;
        line-height: 26px;
        /* identical to box height, or 217% */

        letter-spacing: 0.428571px;
        text-transform: uppercase;

        color: #D2132A;
    }

    .cards__grid{
        width: 100%;
        height: 100%;
        display: flex;
        flex-wrap: wrap;
        max-width: 70%;

    }

    .card{
        background: transparent;
        padding: 0 1em;
        width: 200px;
        height: 300px;
        border-radius: 5px;
        

        display: flex;
        flex-direction: column;
        justify-content: flex-start;

        &__inner{   
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
              
        &__link{
            font-family: $archivo-regular;
            font-size: $default-font-size;
            line-height: $default-font-size + 1;
    
            color: #383030;
            margin-top: 10px;
        } 

        
        img{
            border-radius: 5px;
        }
    }

    .card:nth-child(2), .card:nth-child(5), .card:nth-child(8){
        height: 350px;
        justify-content: flex-end;
    }

}

.section__services{

    background: #170119;

    .services__grid{
        display: grid;
        grid: auto / 1fr;
        gap: 10px;
        grid-gap: 10px;
    }


    .section__services-title{
        font-family: $archivo-black;
        font-size: 40px;
        line-height: 40px;
        /* identical to box height, or 100% */
        margin: 40px 0;

        color: #FFFFFF;

    }

    &--column-1, &--column-2{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 2rem;
    }

    .services__mini-card{
        display: flex;
        width: 100%;
        
        background: rgba(100, 60, 90, 0.2);
        border-radius: 12px;
        justify-content: center;
        align-content: center;
        max-width: 173px;
        height: 174px;

        img{
            width: 100%;
            padding: 1em;
        }
    }

    .services__big-card{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        max-width: 633px;
        max-height: 667px;
        width: 100%;
        height: 100%;
        background: radial-gradient(33.5% 27.76% at 50% 50%, rgba(255, 125, 150, 0.287806) 0%, rgba(245, 64, 137, 4e-05) 100%);
        mix-blend-mode: normal;
        box-shadow: -15px 10px 20px rgba(20, 4, 24, 0.400514);  
        position: relative;

        img{
            width: 100%;
            max-width: 421px;
        }

        &-text{
            position: absolute;
            top: 40%;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        
        &-title{
            font-family: $archivo-regular;
            font-weight: bold;
            font-size: 36px;
            line-height: 46px;
            margin-bottom: 20px;
            max-width: 70%;
            /* or 128% */

            text-align: center;

            color: #FFFFFF;

        }

        &-link{
            font-family: $archivo-black;
            font-size: 12px;
            line-height: 26px;
            /* identical to box height, or 217% */

            text-align: center;
            letter-spacing: 0.428571px;
            text-transform: uppercase;

            color: $red;
        }
    }

}

.section__team{

    background: #FFEFF6;;

    &-title{
        margin: $default-font-size + 4;
        margin-top: $default-font-size*2;
        padding-bottom: 20px;
    }

    &-title-text{
        font-family: $archivo-black;
        font-size: 40px;
        line-height: 40px;
        /* identical to box height, or 100% */

        text-align: center;
        text-transform: uppercase;

        color: #383030;
    }

    &-carousel{
        display: flex;
        justify-content: center;
        margin: 20px 0;
    }

    .carousel__inner{
        width: 100%;
        max-width: 1200px;
    }

    .person-card{
        display: flex;
        justify-content: center;

        img{
            border-radius: 5px;
        }
        
    }

    .team__glide-control{
        position: absolute;
        top: 50%;
        border: 0;
        background: transparent;

        font-family: $archivo-black;
        font-weight: bold;
        font-size: 12px;
        line-height: 13px;
        /* identical to box height */

        text-transform: uppercase;

        color: #161415;

        &:focus{
            outline: 0;
        }

        &--prev{
            padding-left: 10px;
            left: 0;
        }

        &--next{
            padding-right: 10px;
            right: 0;
        }
    }
}

.footer{
    display: flex;
    flex-flow: column;
    width: 100%;
    background: #170119;
    transition:  all 1s 0.5s;    

    &__contact{
        display: flex;
        width: 100%;
        min-height: 100px;
        justify-content: center;
        align-items: center;
        transition:  all 0.4s 0.5s;
        background: linear-gradient(to right, #170119 50%, red 50% );
        background-size: 300% 100%;
        background-position: left bottom;
        background-position-x: 0px;
        background-position-y: 100%;

        &:hover{
            background: linear-gradient(to left, red 50%, #170119 50%);
            background-size: 300% 100%;
            background-position:right bottom;
            background-position-x: 100%;
            background-position-y: 100%;
            transition:  all 0.4s 0.5s;
        }

        &:hover .contact__arrow-icon img{
            transform: rotate(45deg);
            transition: all 0.6s 0.5s;
        }
    }

    &__contact-inner{
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 50px 10px;
    }
    
    .contact__button{
        border: 0;
        background: transparent;
        font-family: $archivo-regular;
        font-weight: bold;
        font-size: 32px;
        line-height: 46px;
        /* or 144% */

        text-align: center;

        color: #FFFFFF;
    }

    .contact__arrow-icon{
        margin: 0 10px;

        img{
            transition: all 0.6s 0.5s;
        }
        
    }



    &__credits{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        margin: $default-font-size - 6 50px;
    }

    .copyright{
        
        &__text{
        font-family: $archivo-regular;
        font-size: 14px;
        line-height: 26px;
        /* identical to box height, or 186% */

        color: #FFFFFF;
        }
    }

    .nextpage{

        display: flex;
        align-items: center;

        img{
            padding-right: 7px;
        }

        &__link{
            font-family: $archivo-regular;
            font-weight: bold;
            font-size: 12px;
            line-height: 26px;
            /* identical to box height, or 217% */

            text-transform: uppercase;

            color: #FFFFFF;
            padding-left: 7px;
        }
    }
}


$breakpoint-small: 576px;
$breakpoint-tablet: 768px;
$breakpoint-desktop: 1024px;
$breakpoint-large: 1300px;



@media (min-width: $breakpoint-small){

    .screen{
        .text__title{
            font-size: $default-font-size*6;
            line-height: $default-font-size*6 + 10;
        }

        .image__container{
            bottom: 20%;
            padding: $default-font-size;
            .text__image{
                max-width: 450px;
            }
        }
    }

    .footer{

        &__credits{
            flex-direction: row;
        }
    }

    .section__services{

        .services__grid{
            display: grid;
            grid: 1fr / repeat(2,  173px);
            gap: 10px;
            grid-gap: 10px;
        }
    }
    
}

@media screen and (min-width: $breakpoint-tablet){

    .header{
        padding: 0 55px;
    }

    .screen{
        .text__title{

           
            font-size: $default-font-size*8;
            line-height: $default-font-size*8+ 10;
        }

        .image__container{
            bottom: 10%;
            padding: $default-font-size;
            .text__image{
                max-width: 678px;
            }
        }
    }

    .section__services{

        .services__grid{
            display: grid;
            grid: 1fr / repeat(3, 173px);
            gap: 4px;
            grid-gap: 4px;
        }
    }
    
}

@media screen and (min-width: $breakpoint-desktop){

    .container{

        &__grid{
            display: grid;
            
    
            &-2__col{
                grid: 1fr/ 1fr 1fr;
            }
        }

    }
    
    .section__clients{
        &-grid{
            grid: 1fr / 30% 70%;
        }

        &-description{
            padding: 2rem 2rem 2rem 55px;
        }

        &-cards{
            padding: 2rem 55px 2rem 2rem;
        }
        
    
    }

    .section__services{
        &--column-1, &--column-2{
            align-items: flex-start;
        }

        &--column-1{
            padding: 2rem 2rem 2rem 55px;
        }

        &--column-2{
            padding: 2rem 55px 2rem 2rem;
        }
    }
    
    
}

@media screen and (min-width: $breakpoint-large){

    .screen{

        .text__title{
            font-size: $default-font-size*10 + 30;
            line-height: 185px;
        }
    }

    .section__team{

        .team__glide-control{

            &--prev{
                
                left: -60px;
            }
    
            &--next{
                padding-right: 10px;
                right: -60px;
            }
        }

    }
}